<template>
  <div class="wrap-flick">
    <div class="container flick-gallery">
      <div class="row">
        <template v-if="!!dataImages.length">
          <div class="col-md-4 flick-gallery__item" v-for="(item, index) in dataImages" :key="index">
            <a
              :href="item.source"
              :title="item.photoId"
              class="flick-gallery__item-inner"
              target="_blank"
            >
              <img :src="item.source" :alt="item.photoId">
            </a>
          </div>
        </template>
        <template v-if="!dataImages.length">
          <p>Loading...</p>
        </template>
      </div>
      <div class="comp-pagination">
        <nav aria-label="Page navigation example">
          <ul class="pagination">

            <li
              class="page-item"
              :class="{'disabled': isFirstPage}"
            >
              <a
                class="page-link"
                aria-label="Previous"
                @click="onClickPage(1)"
              >
                <span aria-hidden="true">&laquo;</span>
                <span class="sr-only">Previous</span>
              </a>
            </li>

            <template
              v-if="totalPage < 6"
            >
              <li
                class="page-item"
                v-for="index in totalPage" :key="index"
              >
                <a
                  class="page-link" 
                  @click="onClickPage(index)"
                  :disabled="index === currentPage ? true : false"
                  :class="{'active': index === currentPage ? true : false}"
                >{{index}}</a>
              </li>
            </template>

            <template
              v-else
            >
              <template v-if="currentPage <= 3">
                <li
                  class="page-item"
                  v-for="index in 5" :key="index"
                >
                  <a
                    class="page-link" 
                    @click="onClickPage(index)"
                    :disabled="index === currentPage ? true : false"
                    :class="{'active': index === currentPage ? true : false}"
                  >{{index}}</a>
                </li>
              </template>

              <template v-else-if="currentPage >= totalPage - 2">
                <li
                  class="page-item"
                  v-for="(item, index) in pageShowLast" :key="index"
                >
                  <a
                    class="page-link" 
                    @click="onClickPage(item)"
                    :disabled="item === currentPage ? true : false"
                    :class="{'active': item === currentPage ? true : false}"
                  >{{item}}</a>
                </li>
              </template>

              <template v-else>
                <li
                  class="page-item"
                  v-for="(item, index) in pageShowMid" :key="index"
                >
                  <a
                    class="page-link" 
                    @click="onClickPage(item)"
                    :disabled="item === currentPage ? true : false"
                    :class="{'active': item === currentPage ? true : false}"
                  >{{item}}</a>
                </li>
              </template>

            </template>
            <li
              class="page-item"
              :class="{'disabled': isLastPage}"
            >
              <a
                class="page-link"
                aria-label="Next"
                @click="onClickPage(totalPage)"
              >
                <span aria-hidden="true">&raquo;</span>
                <span class="sr-only">Next</span>
              </a>
            </li>
          </ul>
        </nav>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from "axios";

// const URL_API = "http://localhost:3000/data";
const URL_API = "/flick/data/images.json";

export default {
  props: {
    msg: String
  },
  computed: {
    isFirstPage() {
      return this.currentPage === 1;
    },
    isLastPage() {
      return this.currentPage === this.totalPage;
    },
    pageShowLast() {
      let arrs = [];
      for(let i = 1; i <= this.totalPage; i++) {
        if(i >= (this.totalPage - 4)) {
          arrs.push(i);
        }
      }

      return arrs;
    },
    pageShowMid() {
      let arrs = [];
      for(let i = 1; i <= this.totalPage; i++) {
        if(i >= (this.currentPage - 2) && i <= (this.currentPage + 2)) {
          arrs.push(i);
        }
      }

      return arrs;
    },
  },
  data() {
    return {
      dataImages: [],
      message: "",
      currentPage: 1,
      totalPage: 1
    }
  },
  created() {
    this.message = this.msg;
    let dataParams = {
      params: {
        page: 1
      },
      url: URL_API
    };
    
    Axios.get(dataParams.url, {
      params: dataParams.params
    })
      .then(res => {
        this.dataImages = res.data.data;
        this.currentPage = res.data.page;
        this.totalPage = res.data.pages;
      })
      .catch(error => console.log(error))
  },
  methods: {
    onClickPage(pageSelect) {
      this.currentPage = pageSelect;
      let dataParams = {
        params: {
          page: this.currentPage
        },
        url: URL_API
      }
      Axios.get(dataParams.url, {
        params: dataParams.params
      })
        .then(res => {
          this.dataImages = res.data.data;
        })
        .catch(error => console.log(error))
    }
  }
};
</script>

<style lang="scss">
@import "FlickGallery.scss";
</style>